// client/src/tabs/ReportsTab.jsx
import React from 'react'
import ChartsAll from '../components/ChartsAll'
import { qp } from '../shared/ui'

export default function ReportsTab({ project }){
  const chartsRef=React.useRef(null)
  return (
    <>
      <div className="card">
        <div className="card__title">Exportaes</div>
        <div className="row">
          <div className="muted">CSV, XLSX (relatrios e detalhe) e PDF.</div>
          <div style={{justifySelf:'end',display:'flex',gap:8}}>
            <button className="btn" onClick={()=>window.location.href=qp('/api/export.csv', project)}>CSV</button>
            <button className="btn" onClick={()=>window.location.href=qp('/api/reports.xlsx', project)}>XLSX Relatrios</button>
            <button className="btn" onClick={()=>window.location.href=qp('/api/export.xlsx', project)}>XLSX Detalhe</button>
            <button className="btn" onClick={()=>window.location.href=qp('/api/reports.pdf', project)}>PDF</button>
            <button className="btn" onClick={()=>chartsRef.current?.reload?.()}>Atualizar relatrios</button>
          </div>
        </div>
      </div>
      <ChartsAll ref={chartsRef} project={project}/>
    </>
  )
}
